import React from "react";
import "./SkillCard.css";

function SkillCard({ skill }: Props) {
  if (!skill.fontAwesome.startsWith("fab")) {
    let src: NodeRequire = require("../../assets/typescript_gray.png");
    if (skill.fontAwesome === "Spring") {
      src = require("../../assets/spring_gray.png");
    }
    if (skill.fontAwesome === "Tensorflow") {
      src = require("../../assets/tensorflow.png");
    }

    return (
      <div className="skill-card-main">
        <div className="skill-icon">
          <div className="img-box">
            <img src={`${src}`} alt={skill.fontAwesome} />
          </div>
        </div>
        <div className="skill-text">
          <p>{skill.text}</p>
        </div>
        <div className="skill-progress-bar">
          <span style={{ width: `${skill.proficiency}%` }}></span>
          <p>{skill.proficiency}%</p>
        </div>
      </div>
    );
  }

  return (
    <div className="skill-card-main">
      <div className="skill-icon">
        <i className={skill.fontAwesome}></i>
      </div>
      <div className="skill-text">
        <p>{skill.text}</p>
      </div>
      <div className="skill-progress-bar">
        <span style={{ width: `${skill.proficiency}%` }}></span>
        <p>{skill.proficiency}%</p>
      </div>
    </div>
  );
}
interface Props {
  skill: {
    fontAwesome: string;
    text: string;
    proficiency: number;
  };
}
export default SkillCard;
